.Navigation {
  color: black;
  background-color: white;
  width: 100%;
  padding: 5px;
  position: sticky;
  top: 0;
  z-index: 999;
}

ul.NavigationMenus {
  display: flex;
  align-items: center;
  padding: 0;
}

ul.NavigationMenus li {
  list-style: none;
  margin-right: 30px;
}

ul.NavigationMenus li a {
  text-decoration: none;
  font-size: 16px;
  color: darkslategray;
  position: relative;
  padding-bottom: 5px;
}

ul.NavigationMenus li a:after {
  content: "";
  display: block;
  position: absolute;
  background: darkgreen;
  height: 1px;
  width: 0%;
  bottom: 0;
  transition: 0.3s;
  margin: 0px auto;
  left: 0;
  right: 0;
}

ul.NavigationMenus li a:hover:after {
  width: 100%;
  transition: 0.3s;
}
